.modal-static {
  position: fixed;
  top: 50% !important;
  left: 50% !important;
  margin-top: -100px;
  margin-left: -100px;
  overflow: visible !important;
}

.modal-static,
.modal-static .modal-dialog,
.modal-static .modal-content {
  width: 200px;
  height: 150px;
}

.modal-static .modal-dialog,
.modal-static .modal-content {
  padding: 0 !important;
  margin: 0 !important;
}

